<template>
  <div>
    <!--      -->
    <h5 v-show="isEmptyObject">  .</h5>

    <div v-show="!isEmptyObject">
      <!--      -->
      <h5 class="text-center resultdetail-h5">  {{ selectedLicense.licenseName }} .</h5>
      <ul>
        <li> : {{ selectedLicense.licenseSeriesName }}</li>
        <li>: {{ selectedLicenseInfo.implNm }}</li>
        <li>: {{ selectedLicenseInfo.instiNm }}</li>
      </ul>

      <!--       -->
      <!--  -->
      <!--  -->
      <div class="d-flex justify-space-around">
        <div class="text-center ma-2">
          <v-btn v-if="!isTodo" @click="addTodo(); snackbar1=true" class="mx-1" small></v-btn>
          <v-snackbar v-model="snackbar1">
              .
             ?
            <template v-slot:action="{ attrs }">
              <v-btn color="pink" text v-bind="attrs" @click="snackbar1 = false"></v-btn>
              <v-btn color="pink" text v-bind="attrs" @click="snackbar1 = false"></v-btn>
            </template>
          </v-snackbar>
        </div>
        <div class="text-center ma-2">
          <v-btn v-if="isTodo" @click="delTodo(); snackbar4=true" class="mx-1" small></v-btn>
          <v-snackbar v-model="snackbar4">
               .
            <template v-slot:action="{ attrs }">
              <v-btn color="pink" text v-bind="attrs" @click="snackbar4 = false"></v-btn>
            </template>
          </v-snackbar>
        </div>


        <!--  -->
        <div class="text-center ma-2">
          <v-btn v-if="!isDoing" @click="addDoing(); snackbar2=true" class="mx-1" small>!</v-btn>
          <v-snackbar v-model="snackbar2">
              .
             ?
            <template v-slot:action="{ attrs }">
              <v-btn color="pink" text v-bind="attrs" @click="snackbar2 = false"></v-btn>
              <v-btn color="pink" text v-bind="attrs" @click="snackbar2 = false"></v-btn>
            </template>
          </v-snackbar>
        </div>
        <div class="text-center ma-2">
          <v-btn v-if="isDoing" @click="delDoing(); snackbar5=true" class="mx-1" small> </v-btn>
          <v-snackbar v-model="snackbar5">
               .
            <template v-slot:action="{ attrs }">
              <v-btn color="pink" text v-bind="attrs" @click="snackbar5 = false"></v-btn>
            </template>
          </v-snackbar>
        </div>


        <!--  -->
        <div class="text-center ma-2">
          <v-btn v-if="!isPass" @click="addPass(); snackbar3=true" class="mx-1" small></v-btn>
          <v-snackbar v-model="snackbar3">
              .
             ?
            <template v-slot:action="{ attrs }">
              <v-btn color="pink" text v-bind="attrs" @click="snackbar3 = false"></v-btn>
              <v-btn color="pink" text v-bind="attrs" @click="snackbar3 = false"></v-btn>
            </template>
          </v-snackbar>
        </div>
        <div class="text-center ma-2">
          <v-btn v-if="isPass" @click="delPass(); snackbar6=true" class="mx-1" small></v-btn>
          <v-snackbar v-model="snackbar6">
               .
            <template v-slot:action="{ attrs }">
              <v-btn color="pink" text v-bind="attrs" @click="snackbar6 = false"></v-btn>
            </template>
          </v-snackbar>
        </div>

      </div>

      <!--     -->
      <div class="d-flex align-center">
        <h5></h5>
        <v-btn class="ml-2" small color="primary" @click="showDetails"></v-btn>
      </div>

      <ul v-show="isDetailsShown">
        <li class="mb-2">: {{ selectedLicenseInfo.summary }}</li>
        <li class="mb-2">: {{ selectedLicenseInfo.job }}</li>
        <li class="mb-2">: {{ selectedLicenseInfo.trend }}</li>
        <li class="mb-2">  : {{ selectedLicenseInfo.career }}</li>
      </ul>

      <LicenseResultHighchart1 v-if="isEmptyChart" :acq_list="acq_list" />
      <LicenseResultHighchart2 />
      <LicenseReview :licenseInfo="selectedLicense" />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import LicenseReview from "./LicenseReview.vue";
import LicenseResultHighchart1 from "./LicenseResultHighchart1";
import LicenseResultHighchart2 from "./LicenseResultHighchart2";

export default {
  name: "LicenseResultDetail",
  components: {
    LicenseReview,
    LicenseResultHighchart1,
    LicenseResultHighchart2,
  },
  created: function () {
    //    
    window.scrollTo(0, 0);

    ////////////////////////////////////
    // selectedLicenseInfo   
    const LICENSE_SERIES_URL = "field_info_all_output.json";
    axios
      .get(
        `http://${this.$store.state.address}/license/` + LICENSE_SERIES_URL
      )
      .then((res) => {
        for (var i = 0; i < res.data.length; i++) {
          var elem = res.data[i];
          if (elem.jmNm === this.selectedLicense.licenseName) {
            this.selectedLicenseInfo = elem;
            break;
          }
        }
      })
      .catch((err) => console.log(err.message));

    /////////////////////////////////////
    //     
    const license_code = this.selectedLicense.licenseCode;
    axios
      .get(
        `http://${this.$store.state.address}/license/license_acq_info_2019.json`
      )
      .then((res) => {
        let r = res.data;
        //        
        for (var i = 0; i < r.length; i++) {
          let jmCd = Number(r[i]["jmCd"]);
          if (license_code === jmCd) {
            console.log(r[i]);
            this.acq_info = r[i];
            break;
          }
        }
      })
      .catch((err) => console.log(err.message));
  },
  mounted: function () {
    //////////////////////////////////////
    //      
    // console.log("LicenseResultDetail Test!");
    axios.get(`http://${this.$store.state.address}:8080/license/getMyLicense`, {
        params: {
          UID: this.hostID,
        },
      })
      .then((res) => {
        const licenses = res.data.object;
        //   mylicense   =>  /
        // console.log(licenses)
        licenses.forEach((obj) => {
          // console.log('')
          if (this.selectedLicense.licenseCode === obj.license.licenseCode) {
            // console.log('')            
            if (obj.licenseStatus === "todo") {
              this.isTodo = true;
              this.myTodoId = obj.id
              // console.log('myTodoId')
              // console.log(this.myTodoId)
            } else if (obj.licenseStatus === "doing") {
              this.isDoing = true;
              this.myDoingId = obj.id
              // console.log('myDoingId')
              // console.log(this.myDoingId)
            } else {
              this.isPass = true;
              this.myPassId = obj.id
            }
          }
        });
      })
      .catch((res) => {
        console.log(res.message);
      });
  },
  computed: {
    isEmptyObject: function () {
      const params = this.selectedLicense;
      return Object.keys(params).length === 0 && params.constructor === Object;
    },
    isEmptyChart: function () {
      const param = this.acq_info;
      return Object.keys(param).length === 0 && param.constructor === Object;
    },
    acq_list: function () {
      const arr = this.acq_info.scholarInfo;
      let sum = 0;
      let result = [];
      for (var i = 0; i < arr.length; i++) {
        sum += Number(arr[i].accumAcquCnt);
      }
      for (i = 0; i < arr.length; i++) {
        let totalCount = Number(arr[i].accumAcquCnt);
        let euhistNm = arr[i].euhistNm;
        if (euhistNm === "") {
          continue;
        } else {
          result.push({
            name: euhistNm,
            y: (totalCount / sum) * 100,
            z: totalCount,
          });
        }
      }
      console.log(result);
      return result;
    },
  },
  watch: {
    passLicenses: function () {
      this.$emit("cntPass", this.passLicenses.length);
    },
    'isTodo': function () {
      axios.get(`http://${this.$store.state.address}:8080/license/getMyLicense`, {
        params: {
          UID: this.hostID,
        },
      })
      .then((res) => {
        const licenses = res.data.object;
        // console.log(licenses)
        licenses.forEach((obj) => {
          if (this.selectedLicense.licenseCode === obj.license.licenseCode) {
            if (obj.licenseStatus === "todo") {
              this.myTodoId = obj.id
            }
          }
        });
      })
      .catch((res) => {
        console.log(res.message);
      });
    },
    'isDoing': function () {
      axios.get(`http://${this.$store.state.address}:8080/license/getMyLicense`, {
        params: {
          UID: this.hostID,
        },
      })
      .then((res) => {
        const licenses = res.data.object;
        // console.log(licenses)
        licenses.forEach((obj) => {
          if (this.selectedLicense.licenseCode === obj.license.licenseCode) {
            if (obj.licenseStatus === "doing") {
              this.myDoingId = obj.id
            }
          }
        });
      })
      .catch((res) => {
        console.log(res.message);
      });
    },
    'isPass': function () {
      axios.get(`http://${this.$store.state.address}:8080/license/getMyLicense`, {
        params: {
          UID: this.hostID,
        },
      })
      .then((res) => {
        const licenses = res.data.object;
        // console.log(licenses)
        licenses.forEach((obj) => {
          if (this.selectedLicense.licenseCode === obj.license.licenseCode) {
            if (obj.licenseStatus === "pass") {
              this.myPassId = obj.id
            }
          }
        });
      })
      .catch((res) => {
        console.log(res.message);
      });
    }
  },
  methods: {
    showDetails: function () {
      this.isDetailsShown = !this.isDetailsShown;
    },
    addTodo() {
      // console.log(" ")
      axios
        .post("http://${this.$store.state.address}:8080/license/addMyLicense", {
          uid: this.hostID,
          licenseCode: this.selectedLicense.licenseCode,
          licenseStatus: "todo",
        })
        .then((res) => {
          console.log(res);
          this.isTodo = !this.isTodo;
        })
        .catch((res) => {
          console.log(res.response);
        });
    },
    addDoing() {
      // console.log(" ")
      axios
        .post("http://${this.$store.state.address}:8080/license/addMyLicense", {
          uid: this.hostID,
          licenseCode: this.selectedLicense.licenseCode,
          licenseStatus: "doing",
        })
        .then((res) => {
          this.isDoing = !this.isDoing;
          console.log(res);
        })
        .catch((res) => {
          console.log(res.response);
        });
    },
    addPass() {
      axios
        .post("http://${this.$store.state.address}:8080/license/addMyLicense", {
          uid: this.hostID,
          licenseCode: this.selectedLicense.licenseCode,
          licenseStatus: "pass",
        })
        .then((res) => {
          this.isPass = !this.isPass;
          console.log(res);
        })
        .catch((res) => {
          console.log(res.response);
        });
    },
    delTodo() {
      axios.post("http://${this.$store.state.address}:8080/license/deleteMyLicense", {
        id: this.myTodoId,
        uid: this.hostID,
        licenseCode: this.selectedLicense.licenseCode
      })
      .then (res => {
        console.log(res)
        this.isTodo = !this.isTodo
      }).catch( res => {
        console.log(res.response.data)
      })
    },
    delDoing() {
      axios.post("http://${this.$store.state.address}:8080/license/deleteMyLicense", {
      id: this.myDoingId,
      uid: this.hostID,
      licenseCode: this.selectedLicense.licenseCode
      })
      .then ( res => {
        console.log(res)
        this.isDoing = !this.isDoing
      }).catch( res => {
        console.log(res.response.data)
      })
    },
    delPass() {
      axios.post("http://${this.$store.state.address}:8080/license/deleteMyLicense", {
      id: this.myPassId,
      uid: this.hostID,
      licenseCode: this.selectedLicense.licenseCode
      })
      .then ( res => {
        console.log(res)
        this.isPass = !this.isPass
      }).catch( res => {
        console.log(res.response.data)
      })
    },
  },
  data: function () {
    return {
      myTodoId: null,
      myDoingId: null,
      myPassId: null,

      snackbar1: false,
      snackbar2: false,
      snackbar3: false,
      snackbar4: false,
      snackbar5: false,
      snackbar6: false,

      //   
      isDetailsShown: false,

      //       
      hostID: this.$store.state.member.loginUID,
      passLicenses: {
        type: Array,
      },
      todoLicenses: {
        type: Array,
      },
      isTodo: false,
      isDoing: false,
      isPass: false,

      //    
      selectedLicenseInfo: {
        type: Object,
      },
      selectedLicense: this.$store.state.license.selectedLicense,
      acq_info: {
        type: Object,
      },
    };
  },
};
</script>

<style scoped>
.resultdetail-h5 {
  color: #fd462e;
  font-family: "Black Han Sans", sans-serif;
}
</style>