<template>
  <div>

    <div class="d-flex flex-column align-center">
      <v-toolbar flat class="mb-3">
        <h5 class="font_l_k"> </h5>
      </v-toolbar>
      <div style="position:relative;">
        <img v-if="host.userThumbnail || userThumbnail" class="thumbnail" :src='"data:"+thumbnailType+";base64," + thumbnail'/>
        <img
          v-if="!host.userThumbnail && !userThumbnail"
          class="thumbnail"
          src="/mystudy/userprofile/default.jpg"
        />
        <!-- <input type="file" accept="image/png, image/jpeg, image/bmp" capture="environment"> -->
        <v-file-input
          prepend-icon="mdi-camera"
          hide-input
          show-size
          counter
          label="file"
          :rules="rules"
          accept="image/png, image/jpeg, image/bmp"
          v-model="userThumbnail"
          style="position: absolute; left: 70%; top: 60%"
        ></v-file-input>
      </div>

      <!-- input form -->
      <v-text-field
        label=""
        v-model="host.userEmail"
        filled
        rounded
        dense
        disabled
        style="width:80%; margin-top:20px;"
      ></v-text-field>

      <v-text-field
        label=""
        v-model="host.userName"
        filled
        rounded
        dense
        style="width:80%;"
      ></v-text-field>
       
      <!-- <v-text-field
        v-model="host.userName"
        label=""
        type="text"
        style="width:70%"
        clearable
        required
      ></v-text-field> -->
    </div>
    <div class="ml-13 mt-3">
      <h5 class="font_k mb-1" style="font-weight: bold"> </h5>
      <p style="font-size:small;">     <span class="color_emp"> </span> ! </p>
      <p class="font_k mb-1" style="font-weight: bold"></p>
    </div>
    <div class="d-flex flex-column align-center">
      <v-select
        :items="subjectsList"
        placeholder=" "
        v-model="subjectElem"
        filled
        label=" "
        rounded
        dense
        style="width:80%;"
      ></v-select>
      <!--  -->
      <div class="d-flex justify-center" style="width: 80%;">  
        <v-text-field
          v-if="subjectElem == null"
          label=""
          v-model="host.major"
          filled
          rounded
          dense
          disabled
          style="width:80%;"
        ></v-text-field>
        <v-autocomplete
          v-if="subjectElem == ''"
          v-model="host.major"
          :items="engineering"
          label=""
          dense
          filled
          rounded
          style="width:80%;"
        ></v-autocomplete>
        <v-autocomplete
          v-if="subjectElem == ''"
          v-model="host.major"
          :items="arts"
          label=""
          dense
          filled
          rounded
          style="width:80%;"
        ></v-autocomplete>
        <v-autocomplete
          v-if="subjectElem == ''"
          v-model="host.major"
          :items="medical"
          label=""
          dense
          filled
          rounded
          style="width:80%;"
        ></v-autocomplete>
        <v-autocomplete
          v-if="subjectElem == ''"
          v-model="host.major"
          :items="society"
          label=""
          dense
          filled
          rounded
          style="width:80%;"
        ></v-autocomplete>
        <v-autocomplete
          v-if="subjectElem == ''"
          v-model="host.major"
          :items="science"
          label=""
          dense
          filled
          rounded
          style="width:80%;"
        ></v-autocomplete>
      </div>
    </div>
    <div class="ml-13">
      <p class="font_k mb-1" style="font-weight: bold"></p>
    </div>
    <div class="d-flex flex-column align-center">
      <v-select
        :items="education"
        v-model="host.education"
        filled
        label=" "
        rounded
        dense
        style="width:80%;"
      ></v-select>

      <!-- <v-select v-model="host.education" :items="education" label=" " style="width:70%"></v-select> -->
    </div>
    <div class="ml-13">
      <p class="font_k mb-2" style="font-weight: bold">   </p>
      <p class="mb-1" style="font-size:small;">   . </p>
    </div>
    <div class="d-flex flex-column align-center">
      <v-autocomplete
        v-model="host.field1"
        :items="desiredFields"
        label=" "
        dense
        filled
        rounded
        style="width:80%;"
      ></v-autocomplete>

      <!-- <v-autocomplete v-model="host.field1" :items="desiredFields" label=" "></v-autocomplete> -->
        <v-autocomplete
        v-model="host.desiredField1"
        :items="desiredFields"
        label="1"
        dense
        filled
        rounded
        style="width:80%"
        ></v-autocomplete>
        <v-autocomplete
        v-model="host.desiredField2"
        :items="desiredFields"
        label="2"
        dense
        filled
        rounded
        style="width:80%"
        ></v-autocomplete>
        <v-autocomplete
        v-model="host.desiredField3"
        :items="desiredFields"
        label="3"
        dense
        filled
        rounded
        style="width:80%"
        ></v-autocomplete>

      <v-card flat width="40%" class="d-flex justify-center mt-5 mb-15">
        <v-btn @click="cancel" rounded block large outlined color="#fd462e"></v-btn>
        <v-btn rounded class="mb-1" @click="update" block large dark color="#fd462e"> </v-btn>
      </v-card>  
    </div>
  </div>
</template>

<script>
import axios from "axios"

export default {
  name: "EditProfile",
  watch: {
    'userThumbnail': function() {
      //    
      this.isImgUpload = true
      console.log(this.isImgUpload)
      //   =>   ,  ,   thumbnail  
      const formData = new FormData();
      formData.append('userThumbnail', this.userThumbnail)
      axios.post('http://${state.address}:8080/getImage', formData, {
        headers: {
          'Content-Type' : 'multipart/form-data'
        }
      })
      .then( res => {
        // console.log(res.data.object.thumbnail) 
        // console.log(res.data.object.thumbnailType)
        // console.log(this.userThumbnail)
        this.thumbnail = res.data.object.thumbnail
        this.thumbnailType = res.data.object.thumbnailType
      })
      .catch( res => {
        console.log(res)
      })
    }
  },
  data(){
    const defaultForm = Object.freeze({
      education: '',
      status: '',
      })
    return{
      loginUID : this.$route.params.UID,
      host: {},
      majorSeq: '',
      //  
      thumbnail: null,
      thumbnailType : null,
      subjectElem: null,
      //   
      userThumbnail: null,
      isImgUpload : false,

      form: Object.assign({}, defaultForm),
      education: [' ', '', '(2)', '(4) ', ' '],
      status: ['', ' ', ' ', ''],
      subjectsList: ['', '', '', '', ''],
      engineering : [],
      arts: [],
      medical: [],
      society: [],
      science: [],
      // subjects: [
      //   {'subject': '', 'value': 100391}, 
      //   {'subject': '', 'value': 100392}, 
      //   {'subject': '', 'value': 100393}, 
      //   {'subject': '', 'value': 100394}, 
      //   {'subject': '', 'value': 100395}, 
      //   {'subject': '', 'value': 100396}, 
      //   {'subject': '', 'value': 100397}
      // ],
      majors: [],
      majorsObject: {
        type: Array
      },
      desiredFields: {
        type: Array
      },
      rules: [
        value => !value || value.size < 16000000 || '  16 MB !',
      ],
    }
  },
  created() {
    // UID   
    axios.post(`http://${this.$store.state.address}:8080/getUser`, {
      id: this.loginUID
    })
    .then(res => {
      // console.log("getUser Success.")
      // console.log(res.data)
      this.host = res.data.object
      this.thumbnail = this.host.userThumbnail
      this.thumbnailType =this.host.imageType
    })
    .catch( function(error) {
      // console.log(this.hostUID)
      console.log(error)
    })

    // desiredFields 
    // console.log('Import desireFields')
    const df = []
    axios.get('http://${this.$store.state.address}/license/ncs_fields_license.json')
      .then(res => {
        console.log(res)
        res.data.forEach(elem => {
          df.push(elem.ncsCategoryName1)
        })
      })
      .catch(err => console.log(err.message))
    this.desiredFields = df
    console.log('')
    console.log(this.desiredFields)

    //   
    axios.get('http://${this.$store.state.address}/setting/majors.json')
    .then( res => {
      // console.log(res.data)
      res.data.forEach((elem) => {
        // console.log(elem)
        if(elem.subject == '') {
          this.engineering.push(elem.major)
        } else if(elem.subject == '') {
          this.arts.push(elem.major)
        } else if(elem.subject == '') {
          this.medical.push(elem.major)
        } else if(elem.subject == '') {
          this.society.push(elem.major)
        } else {
          this.science.push(elem.major)
        } 
      })
      // console.log(this.society)
    })
    .catch( res => {
      console.log(res)
    })
  },
  methods: {
    cancel() {
      this.$router.push({name: 'Setting', params: { UID: this.$store.state.member.loginUID }})
    },
    update() {
      const formData = new FormData();
      formData.append('id', this.host.id)
      formData.append('userEmail', this.host.userEmail)
      formData.append('userName', this.host.userName)
      formData.append('userContent', this.host.userContent)
      formData.append('password', this.host.password)
      formData.append('major', this.host.major)
      formData.append('education', this.host.education)
      formData.append('field1', this.host.field1)
      formData.append('desiredField1', this.host.desiredField1)
      formData.append('desiredField2', this.host.desiredField2)
      formData.append('desiredField3', this.host.desiredField3)
      formData.append('isSecret', this.host.isSecret)
      //    
      if(this.isImgUpload) {
        formData.append('userThumbnail', this.userThumbnail)   
        axios.post(`http://${this.$store.state.address}:8080/updateMyInfoWithImage`, formData, {
          headers: {
            'Content-Type' : 'multipart/form-data'
          }
        })
        .then( res => {
          alert(' .')
          console.log(res) 
        })
        .catch( res => {
          console.log('err')
          console.log(res)
        })
        .finally(function(){
          console.log('fin')
        })
      } else {
        //    
        axios.post(`http://${this.$store.state.address}:8080/updateMyInfoNoImage`, formData, {
          headers: {
            'Content-Type' : 'multipart/form-data'
          }
        })
        .then( res => {
          alert(' .')
          console.log(res) 
        })
        .catch( res => {
          console.log('err')
          console.log(res)
        })
      }
      
    },
  }
}
</script>

